th: 75% !default;\n$input-height: 55px;\n// $input-height-small: 35px;\n\n$column-gutter: 1.75%;\n\n$sideways-tabs-width: 200px;\n\n$z-indexes: (\n  zero: 0,\n  default: 1,\n  overContent: 2,\n  hoverOverContent: 3,\n\n  tableGroup: 10,\n  fixedTableHeader: 11,\n\n  modalOverlay: 20,\n  modalContent: 21,\n\n  tooltip: 30,\n\n  dropdownOverlay: 40,\n  dropdownContent: 41,\n\n  loadingOverlay: 50,\n  loadingContent: 51\n);\n\n// Usage Example:\n// @media only screen and (min-width: map-get($breakpoints, '--viewport-*')) {\n// }\n$breakpoints: (\n  '--viewport-4':  480px,\n  '--viewport-7':  768px,\n  '--viewport-9':  992px,\n  '--viewport-12': 1281px,\n);\n\n///Computes the \"brightness\" of a color\n@function brightness($color) {\n  @if type-of($color) == color {\n    @return (red($color) * 0.299 + green($color) * 0.587 + blue($color) * 0.114) / 255 * 100%;\n  }\n  @else {\n    @return unquote(\"brightness(#{$color})\");\n  }\n}\n\n\n///Select the more readable foreground color for a given background color.\n@function contrast-color($color, $dark: $contrasted-dark, $light: $contrasted-light) {\n  @if $color == null {\n    @return null;\n  }\n  @else {\n    $color-brightness: brightness($color);\n    $dark-text-brightness: brightness($dark);\n    $light-text-brightness: brightness($light);\n    @return if(abs($color-brightness - $light-text-brightness) > abs($color-brightness - $dark-text-brightness), $light, $dark);\n  }\n}\n\n@function add-z-index($key, $value) {\n  @return map-merge($z-indexes, ($key: $value));\n}\n\n@function z-index($key) {\n  @if map-has-key($z-indexes, $key) {\n    @return map-get($z-indexes, $key);\n  }\n\n  @warn \"Unknown key `#{$key}` in $z-indexes\";\n  @return null;\n}\n\n// _decimal.scss | MIT License | gist.github.com/terkel/4373420\n\n// Round a number to specified digits.\n//\n// @param  {Number} $number A number to round\n// @param  {Number} [$digits:0] Digits to output\n// @param  {String} [$mode:round] (round|ceil|floor) How to round a number\n// @return {Number} A rounded number\n// @example\n//     decimal-round(0.333)    => 0\n//     decimal-round(0.333, 1) => 0.3\n//     decimal-round(0.333, 2) => 0.33\n//     decimal-round(0.666)    => 1\n//     decimal-round(0.666, 1) => 0.7\n//     decimal-round(0.666, 2) => 0.67\n//\n@function decimal-round ($number, $digits: 0, $mode: round) {\n  $n: 1;\n  // $number must be a number\n  @if type-of($number) != number {\n      @warn '#{ $number } is not a number.';\n      @return $number;\n  }\n  // $digits must be a unitless number\n  @if type-of($digits) != number {\n      @warn '#{ $digits } is not a number.';\n      @return $number;\n  } @else if not unitless($digits) {\n      @warn '#{ $digits } has a unit.';\n      @return $number;\n  }\n  @for $i from 1 through $digits {\n      $n: $n * 10;\n  }\n  @if $mode == round {\n      @return round($number * $n) / $n;\n  } @else if $mode == ceil {\n      @return ceil($number * $n) / $n;\n  } @else if $mode == floor {\n      @return floor($number * $n) / $n;\n  } @else {\n      @warn '#{ $mode } is undefined keyword.';\n      @return $number;\n  }\n}\n\n// Ceil a number to specified digits.\n//\n// @param  {Number} $number A number to round\n// @param  {Number} [$digits:0] Digits to output\n// @return {Number} A ceiled number\n// @example\n//     decimal-ceil(0.333)    => 1\n//     decimal-ceil(0.333, 1) => 0.4\n//     decimal-ceil(0.333, 2) => 0.34\n//     decimal-ceil(0.666)    => 1\n//     decimal-ceil(0.666, 1) => 0.7\n//     decimal-ceil(0.666, 2) => 0.67\n//\n@function decimal-ceil ($number, $digits: 0) {\n  @return decimal-round($number, $digits, ceil);\n}\n\n// Floor a number to specified digits.\n//\n// @param  {Number} $number A number to round\n// @param  {Number} [$digits:0] Digits to output\n// @return {Number} A floored number\n// @example\n//     decimal-floor(0.333)    => 0\n//     decimal-floor(0.333, 1) => 0.3\n//     decimal-floor(0.333, 2) => 0.33\n//     decimal-floor(0.666)    => 0\n//     decimal-floor(0.666, 1) => 0.6\n//     decimal-floor(0.666, 2) => 0.66\n//\n@function decimal-floor ($number, $digits: 0) {\n  @return decimal-round($number, $digits, floor);\n}\n\n@function sizzle-gradient($color) {\n  $angle: 135deg;\n  $startPos: 0%;\n  $start: 0.3;\n  $middlePos: 110px;\n  $middle: 0.1;\n  $endPos: 100%;\n  $end: 0;\n\n  @return transparent linear-gradient(#{$angle},\n      #{rgba($color, $start)} #{$startPos},\n      #{rgba($color, $middle)} #{$middlePos},\n      #{rgba($color, $end)} #{$endPos}\n  ) 0% 0% no-repeat padding-box;\n}\n\n// -----------------------------------------------------------------------------\n// This file contains all application-wide Sass mixins.\n// -----------------------------------------------------------------------------\n\n/// Clear inner floats\n@mixin clearfix() {\n  &:before,\n  &:after {\n    content: \" \"; // 1\n    display: table; // 2\n  }\n  &:after {\n    clear: both;\n  }\n}\n\n@mixin list-unstyled {\n  margin: 0;\n  padding: 0;\n  list-style-type: none;\n}\n\n@mixin no-select {\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n@mixin no-resize {\n  resize : none;\n}\n\n@mixin hand {\n  cursor : pointer;\n  cursor : hand;\n}\n\n@mixin fixed {\n  table-layout : fixed;\n}\n\n@mixin clip {\n  text-overflow : ellipsis;\n  overflow      : hidden;\n  white-space   : nowrap;\n  word-wrap     : break-word;\n}\n\n@mixin force-wrap {\n  word-wrap : break-word;\n  white-space: normal;\n}\n\n@mixin bordered-section {\n  border-bottom: 1px solid var(--border);\n  margin-bottom: 20px;\n  padding-bottom: 20px;\n}\n\n@mixin section-divider {\n  margin-bottom: 20px;\n  margin-top: 20px;\n}\n\n.clearfix         { @include clearfix; }\n.list-unstyled    { @include list-unstyled }\n.no-select        { @include no-select }\n.no-resize        { @include no-resize }\n.hand             { @include hand }\n.fixed            { @include fixed }\n.clip             { @include clip }\n.force-wrap       { @include force-wrap }\n.bordered-section { @include bordered-section }\n.section-divider  { @include section-divider }\n\n/// Sets the specified background color and calculates a dark or light contrasted text color.\n@mixin contrasted($background-color, $dark: $contrasted-dark, $light: $contrasted-light) {\n  color: contrast-color($background-color, $dark, $light);\n\n  &:hover {\n    text-decoration: underline;\n    color: var(--body-text);\n  }\n}\n\n/// Sets base color and darkens bg on hover\n@mixin bg-lighten($bg) {\n  background: $bg;\n  * {\n    background:lighten($bg,20%);\n  }\n}\n\n@mixin link-color($color, $hover) {\n  @if not($hover) {\n    $hover: $color;\n  }\n\n  color: $color;\n\n  &:hover\n   {\n    text-decoration: underline;\n    color: var(--body-text);\n  }\n}\n\n@mixin icon-rotate($degrees, $rotation) {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=#{$rotation});\n  -webkit-transform: rotate($degrees);\n      -ms-transform: rotate($degrees);\n          transform: rotate($degrees);\n}\n\n@mixin icon-flip($horiz, $vert, $rotation) {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=#{$rotation});\n  -webkit-transform: scale($horiz, $vert);\n      -ms-transform: scale($horiz, $vert);\n          transform: scale($horiz, $vert);\n}\n\n@mixin input-status-color {\n  &:not(.focused) {\n    &.success {\n      border: solid 1px var(--success);\n      input, .selected {\n        color: var(--success);\n      }\n\n      .vs__actions:after {\n        color: var(--success);\n      }\n    }\n\n    &.warning {\n      border: solid 1px var(--warning);\n      input, .selected {\n        color: var(--warning);\n      }\n\n      .vs__actions:after {\n        color: var(--warning);\n      }\n    }\n\n    &.error {\n      border: solid 1px var(--error);\n      input, .selected {\n        color: var(--error);\n      }\n\n      .vs__actions:after {\n        color: var(--error);\n      }\n    }\n  }\n}\n\n@mixin form-focus {\n  // Focus for form like elements (not to be confused with basic :focus style)\n  outline: none;\n  box-shadow: 0 0 0 var(--outline-width) var(--outline);\n  background: var(--input-focus-bg)\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n$height: 110px;\n$side: 15px;\n$margin: 10px;\n$logo: 60px;\n\n.grid {\n  display: flex;\n  justify-content: flex-start;\n  flex-wrap: wrap;\n  margin: 0 -1*$margin;\n\n  @media only screen and (min-width: map-get($breakpoints, '--viewport-4')) {\n    .item {\n      width: 100%;\n    }\n  }\n  @media only screen and (min-width: map-get($breakpoints, '--viewport-7')) {\n    .item {\n      width: calc(50% - 2 * #{$margin});\n    }\n  }\n  @media only screen and (min-width: map-get($breakpoints, '--viewport-9')) {\n    .item {\n      width: calc(33.33333% - 2 * #{$margin});\n    }\n  }\n  @media only screen and (min-width: map-get($breakpoints, '--viewport-12')) {\n    .item {\n      width: calc(25% - 2 * #{$margin});\n    }\n  }\n\n  .item {\n    height: $height;\n    margin: $margin;\n    padding: $margin;\n    position: relative;\n    border-radius: calc( 1.5 * var(--border-radius));\n\n    &:hover {\n      box-shadow: 0 0 30px var(--shadow);\n      transition: box-shadow 0.1s ease-in-out;\n      cursor: pointer;\n    }\n\n    .side-label {\n      transform: rotate(180deg);\n      position: absolute;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      min-width: calc(1.5 * var(--border-radius));\n      width: $side;\n      border-top-right-radius: calc( 1.5 * var(--border-radius));\n      border-bottom-right-radius: calc( 1.5 * var(--border-radius));\n\n      label {\n        text-align: center;\n        writing-mode: tb;\n        height: 100%;\n        padding: 0 2px;\n        display: block;\n        white-space: no-wrap;\n        text-overflow: ellipsis;\n      }\n    }\n\n    .logo {\n      text-align: center;\n      position: absolute;\n      left: $side+$margin;\n      top: ($height - $logo)/2;\n      width: $logo;\n      height: $logo;\n      border-radius: calc(2 * var(--border-radius));\n      overflow: hidden;\n      background-color: white;\n\n      img {\n        width: $logo - 4px;\n        height: $logo - 4px;\n        object-fit: contain;\n        position: relative;\n        top: 2px;\n      }\n    }\n\n    .side-label {\n      font-size: 10px;\n    }\n\n    &.rancher {\n      background: var(--app-rancher-bg);\n\n      .side-label {\n        background-color: var(--app-rancher-accent);\n        color: var(--app-rancher-accent-text);\n      }\n      &:hover {\n        background: var(--app-rancher-accent);\n      }\n    }\n\n    &.partner {\n      background: var(--app-partner-bg);\n      .side-label {\n        background-color: var(--app-partner-accent);\n        color: var(--app-partner-accent-text);\n      }\n      &:hover {\n        background: var(--app-partner-accent);\n      }\n    }\n\n    // @TODO figure out how to templatize these\n    &.color1 {\n      background: var(--app-color1-bg);\n      .side-label { background-color: var(--app-color1-accent); color: var(--app-color1-accent-text); }\n      &:hover { background: var(--app-color1-accent); }\n    }\n    &.color2 {\n      background: var(--app-color2-bg);\n      .side-label { background-color: var(--app-color2-accent); color: var(--app-color2-accent-text); }\n      &:hover { background: var(--app-color2-accent); }\n    }\n    &.color3 {\n      background: var(--app-color3-bg);\n      .side-label { background-color: var(--app-color3-accent); color: var(--app-color3-accent-text); }\n      &:hover { background: var(--app-color3-accent); }\n    }\n    &.color4 {\n      background: var(--app-color4-bg);\n      .side-label { background-color: var(--app-color4-accent); color: var(--app-color4-accent-text); }\n      &:hover { background: var(--app-color4-accent); }\n    }\n    &.color5 {\n      background: var(--app-color5-bg);\n      .side-label { background-color: var(--app-color5-accent); color: var(--app-color5-accent-text); }\n      &:hover { background: var(--app-color5-accent); }\n    }\n    &.color6 {\n      background: var(--app-color6-bg);\n      .side-label { background-color: var(--app-color6-accent); color: var(--app-color6-accent-text); }\n      &:hover { background: var(--app-color6-accent); }\n    }\n    &.color7 {\n      background: var(--app-color7-bg);\n      .side-label { background-color: var(--app-color7-accent); color: var(--app-color7-accent-text); }\n      &:hover { background: var(--app-color7-accent); }\n    }\n    &.color8 {\n      background: var(--app-color8-bg);\n      .side-label { background-color: var(--app-color8-accent); color: var(--app-color8-accent-text); }\n      &:hover { background: var(--app-color8-accent); }\n    }\n\n    &:hover {\n      background-position: right center;\n    }\n\n    .name {\n      white-space: nowrap;\n      overflow: hidden;\n      text-overflow: ellipsis;\n      line-height: $height - (2 * $margin);\n      margin: 0;\n      margin-left: $side+$logo+$margin;\n    }\n\n    &.has-description .name {\n      margin-top: $margin;\n      line-height: initial;\n    }\n\n    .description {\n      margin-top: $margin;\n      margin-left: $side+$logo+$margin;\n      margin-right: $margin;\n      display: -webkit-box;\n      -webkit-box-orient: vertical;\n      -webkit-line-clamp: 3;\n      line-clamp: 3;\n      overflow: hidden;\n      text-overflow: ellipsis;\n      color: var(--text-muted);\n    }\n  }\n}\n"]}]);
